  "à¸™à¸­à¸™à¸à¹ˆà¸­à¸™": "à¹€à¸ˆà¸£à¸´à¸à¸žà¸£ à¸Šà¸´à¸•à¸±à¸‡à¹€à¸¡ à¹‚à¸›à¹‰à¸‡ à¸£à¸§à¸¢à¹†"
